/** @jsx jsx */
import React from 'react'
import { css, jsx } from '@emotion/core'
import { Container, Typography } from 'reus'
import { StaticQuery, graphql } from 'gatsby'
import styled from '@emotion/styled'
import Img from 'gatsby-image'

const Feature = () => {
  const Heading = styled.h2`
    color: ${({theme}) => theme.color.secondary};
    font-size: 40px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.2;
    letter-spacing: normal;
    text-align: center;
  `
  const ImgWrapper = styled.div`
    border-radius: 11px;
    box-shadow: 0px 8px 36px #d8d8d8;
    overflow: hidden;
  `
  const Desc = styled.p`
    font-size: 26px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.54;
    letter-spacing: normal;
    text-align: center;
  `
  const FeatureWrapped = styled.div`
    label: feature;
    padding: 70px 0 30px 0px;
  `
  const HeadWrapper = styled.div`
    margin-bottom: 80px;
    max-width: ${({theme}) => theme.breakPoints.md};
    margin-left: auto;
    margin-right: auto;
  `
  return (
    <StaticQuery
      query={graphql`
        query FeatureQuery {
          attendanceLeave: file(relativePath: { eq: "attendance-leave-modana.png" }) {
            childImageSharp {
              fluid(maxWidth: 600) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      `}
      render={data => {
        return (
          <FeatureWrapped>
            <Container lg gutter={16}>
              <HeadWrapper>
                <Typography 
                  tag="h2" 
                  type="headline"
                  css={theme =>  css`color: ${theme.color.secondary};`}
                  align="center">
                  Simple Human Resource system: Database, Payroll, Lending. We have it all!
                </Typography>
                <Typography
                  tag="p" 
                  type="intro" 
                  align="center">
                  The services that help your company start from Managing your employee information, Attendance, Payroll disbursement, and Employee lending.
                </Typography>
              </HeadWrapper>
              <div>
                <ImgWrapper>
                  <Img fluid={data.attendanceLeave.childImageSharp.fluid}/>
                </ImgWrapper>
              </div>
            </Container>
          </FeatureWrapped>
        )
      }}
    />
  )
}

export default Feature